<!--  -->
<template>
  <div>
    <nav-bar>
      <template v-slot:left>
        <div>
          <button class="btn" @click="back">
            <img src="~assets/images/home/back.svg" @load="imgOnload" alt="" />
          </button>
        </div>
      </template>
      <template v-slot:middle>
        <tab-control :titles="titles" @tabClick="tabClick" class="tabcontrol"></tab-control>
      </template>
      <template v-slot:right> </template>
    </nav-bar>
  </div>
</template>

<script>
import NavBar from "components/common/NavBar/NavBar";
import TabControl from "components/content/TabControl/TabControl";

export default {
  data() {
    return {
      iid: null,
      titles: ["商品", "参数", "评论", "推荐"],
      currentIndex: 0,
    };
  },
  methods: {
    back() {
      this.$router.back();
    },
    tabClick(index){
      this.currentIndex = index;
      this.$emit("titleClick",index);
    },
    imgOnload() {
      this.$emit("imgOnload");
    }
  },
  components: {
    NavBar,
    TabControl,
  }
};
</script>
<style scoped>
/* @import url(); 引入css类 */
.btn {
  width: 50%;
  height: 22px;
  border: 0;
  background-color: #fff;
  position: relative;
  top: 8px;
  left: 8px;
}
.btn img {
  width: 100%;
  height: 100%;
}
.tabcontrol {
  font-size: 15px;
}
</style>