<!-- 详情页面 -->
<template>
  <div id="detail">
    <detail-nav-bar @titleClick="changeClick"></detail-nav-bar>
    <scroll
      @scroll="contentScroll"
      class="content"
      ref="scroller"
      :probe-type="3"
      :pull-up-load="true"
    >
      <div>
        <detail-swiper
          @imgOnload="imgOnload"
          :banners="banners"
          v-if="banners.length"
        ></detail-swiper>
        <detail-base-info
          @imgOnload="imgOnload"
          :goods="goods"
          v-if="goods != null"
        ></detail-base-info>
        <detail-shop-info
          @imgOnload="imgOnload"
          :shop="shopInfo"
          v-if="goods != null"
        ></detail-shop-info>
        <detail-goods-info
          @imgOnload="imgOnload"
          :detailInfo="detailInfo"
          v-if="detailInfo != null"
        ></detail-goods-info>
        <detail-params-info
          @imgOnload="imgOnload"
          ref="params"
          :paramInfo="paramInfo"
          v-if="paramInfo != null"
        ></detail-params-info>
        <detail-comment-info
          @imgOnload="imgOnload"
          ref="comment"
          :commentInfo="commentInfo"
        ></detail-comment-info>
        <goods-list
          @imgOnload="imgOnload"
          ref="goodslist"
          :goods="recommends"
        ></goods-list>
      </div>
    </scroll>
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
  </div>
</template>

<script>
import DetailNavBar from "views/detail/childComps/DetailNavBar";
import DetailSwiper from "views/detail/childComps/DetailSwiper";
import DetailBaseInfo from "views/detail/childComps/DetailBaseInfo";
import DetailShopInfo from "views/detail/childComps/DetailShopInfo";
import DetailGoodsInfo from "views/detail/childComps/DetailGoodsInfo";
import DetailParamsInfo from "views/detail/childComps/DetailParamsInfo";
import DetailCommentInfo from "views/detail/childComps/DetailCommentInfo";

import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend,
} from "network/detail";
import { log } from "util";
import scroll from "components/common/scroll/scroll";
import GoodsList from "components/content/goods/GoodsList";
import BackTop from "components/content/BackTop/BackTop";

export default {
  name: "Detail",
  data() {
    return {
      iid: null,
      titles: ["商品", "参数", "评论", "推荐"],
      banners: [],
      goods: null,
      shopInfo: null,
      detailInfo: null,
      paramInfo: null,
      commentInfo: {},
      recommends: [],
      isShowBackTop: false,
      currentOffsetTop: null,
    };
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamsInfo,
    DetailCommentInfo,
    scroll,
    GoodsList,
    BackTop,
  },
  methods: {
    backClick() {
      this.$refs.scroller.scrollTo(0, 0, 500);
    },
    contentScroll(position) {
      this.$refs.scroller.scroll.refresh();
      this.isShowBackTop = -position.y >= -680 ? true : false;
    },
    changeClick(index) {
      this.$refs.scroller.scrollTo(0, this.currentOffsetTop[index], 500);
    },
    imgOnload() {
      this.$refs.scroller.scroll.refresh();
      this.currentOffsetTop = [0],
      this.currentOffsetTop.push(-this.$refs.params.$el.offsetTop);
      this.currentOffsetTop.push(-this.$refs.comment.$el.offsetTop);
      this.currentOffsetTop.push(-this.$refs.goodslist.$el.offsetTop);
      console.log(this.currentOffsetTop);
    },
  },
  //生命周期 - 创建完成（访问当前this实例）
  created() {
    this.iid = this.$route.params.iid;
    getDetail(this.iid).then(async (res) => {
      // 1.获取顶部的图片轮播数据
      this.banners = await res.result.itemInfo.topImages;
      let result = res.result;
      // 2.获取商品信息
      this.goods = new Goods(
        result.itemInfo,
        result.columns,
        result.shopInfo.services
      );

      // 3.创建店铺信息的对象
      this.shopInfo = new Shop(result.shopInfo);

      // 4.保存商品的详情数据
      this.detailInfo = result.detailInfo;

      // 5.获取参数的信息
      this.paramInfo = new GoodsParam(
        result.itemParams.info,
        result.itemParams.rule
      );

      //6.获取评论信息
      if (result.rate.cRate !== 0) {
        this.commentInfo = result.rate.list[0];
      }
    });

    //请求推荐数据
    getRecommend().then((res) => {
      this.recommends = res.data.list;
    });
  },
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {},
  updated() {
    this.$refs.scroller.scroll.refresh();
  },
};
</script>
<style scoped>
/* @import url(); 引入css类 */

.content {
  overflow: hidden;
  position: absolute;
  top: 44px;
  bottom: 49px;
  right: 0;
  left: 0;
}
</style>