<!--  -->
<template>
  <swiper class="detail-swiper">
    <swiper-item v-for="(item, index) of banners" :key="index">
      <img :src="item" @load="imgOnload" alt="" />
    </swiper-item>
  </swiper>
</template>

<script>
import { SwiperItem, Swiper } from "components/common/swiper";

export default {
  data() {
    return {};
  },
  props: {
    banners: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    imgOnload() {
      this.$emit("imgOnload");
    },
  },
  components: {
    SwiperItem,
    Swiper,
  },
};
</script>
<style scoped>
/* @import url(); 引入css类 */
.detail-swiper {
  height: 300px;
  overflow: hidden;
}
</style>